
import Vue from "vue";
export default Vue.extend({
  props:['input' ,  'value'],
  watch:{
    "input.value" :(val) => {
      console.log("val")
      console.log(val)
    }
  },  
  methods:{
    change(val:any){
      this.input.val = val

      // if (this.type == 'number') {
      //   val = parseInt(val)
      // }
      if(this.input.type == 'number') {
        val = val == '' || val == null ? 0 : parseFloat(val)
      }
      this.$emit('input'  ,val)
    }
  }
});
